import classNames from 'classnames';
import React, { ReactEventHandler } from 'react';

import { Loader } from '../Loader';
import styles from './Button.module.scss';

export interface IButtonProps {
    text: string;
    onClick?: ReactEventHandler;
    onMouseEnter?: ReactEventHandler;
    onMouseLeave?: ReactEventHandler;
    url?: string;
    size?: 's' | 'm' | 'l';
    radius?: boolean;
    fullWidth: boolean;
    type?: 'submit' | 'button' | 'reset';
    variant?: 'primary' | 'secondary';
    buttonStyle?: 'normal' | 'hot' | 'cold';
    disabled?: boolean;
    loading?: boolean;
}

export function Button({
    text,
    onClick,
    onMouseEnter,
    onMouseLeave,
    url,
    size = 'm',
    radius = false,
    fullWidth,
    type = 'button',
    variant = 'primary',
    buttonStyle = 'normal',
    loading = false,
    disabled = loading || false,
}: IButtonProps) {
    // const sendClick = useAppDispatch(clickDone);

    return (
        <a
            href={url}
            target="_blank"
            rel="noopener"
            onClick={(e) => {
                // sendClick(text);
                onClick && onClick(e);
            }}
        >
            <button
                className={classNames(styles.button, {
                    [styles.buttonNormal]: buttonStyle === 'normal',
                    [styles.buttonHot]: buttonStyle === 'hot',
                    [styles.buttonCold]: buttonStyle === 'cold',
                    [styles.buttonM]: size === 'm',
                    [styles.buttonL]: size === 'l',
                    [styles.buttonS]: size === 's',
                    [styles.buttonRadius]: radius,
                    [styles.buttonPrimary]: variant === 'primary',
                    [styles.buttonSecondary]: variant === 'secondary',
                    [styles.buttonFullWidth]: fullWidth === true,
                })}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
                type={type}
                disabled={disabled}
            >
                {loading ? (
                    <span className={styles.buttonLoading}>
                        <Loader />
                    </span>
                ) : (
                    text
                )}
            </button>
        </a>
    );
}

export default Button;
